<template>
  <div>
    <div class="tabs">
      <ul>
        <li class="is-active"><a>Studium Generale</a></li>
      </ul>
    </div>
    <FilterButtons @filter-semester="filterSemester" @filter-type="filterType" @reset-filter="resetFilter"/>
    <div class="columns is-multiline">
      <div v-for="lesson in displayData" :key="lesson.title" class="column is-one-third">
        <div class="card">
          <div class="card-content">
            <h1 class="title is-size-5">{{ lesson.title }}</h1>
            <Cards :lesson="lesson"/>
            <Tags :semesters="lesson.requirements"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Tags from '../components/Tags'
import Cards from '../components/Cards'
import FilterButtons from '../components/FilterButtons'
import json from '../assets/data-sg.json'

export default {
  components: {
    Tags,
    Cards,
    FilterButtons
  },
  data() {
    return {
      displayData: json
    }
  },
  methods: {
    filterSemester(semester) {
      this.displayData = this.displayData.filter((e) => {
        return e.requirements.some(x => x.name === 'Phil ' + semester) ||
            e.requirements.some(x => x.name === 'Ges ' + semester) ||
            e.requirements.some(x => x.name === 'Ku ' + semester)
      })
    },
    filterType(type) {
      this.displayData = this.displayData.filter((e) => {
        return e.requirements.some(x => x.name.includes(type))
      })
    },
    resetFilter() {
      this.displayData = json
    }
  }
}
</script>

<style scoped>
.card {
  height: 100%;
}
</style>
